@import "../../../css/module/fn";
@import "../../../css/module/color";

.mui-content {
	background-color: #494949;
	padding-bottom: p2m(20);
}

#searchWrap {
	height: p2m(90);
	padding: 6px;
	* {
		color: #fff !important;
	}
	
	.mui-search {
	
	}
	input[type='search'] {
		background-color: #696969;
		border: 1px solid #999;
	}
}

#tbSchedule {
	width: 100%;
	table-layout: fixed;
	border: none;
	border-collapse: collapse;
	border-top: 1px solid #fff;
	tr {
		> td {
			width: p2m(206);
			height: p2m(165);
			border: 1px solid #eee;
			text-align: center;
			border-top: none;
			border-right: none;
			
			&:nth-of-type(4n+1) {
				font-size: p2m(30);
				line-height: p2m(50);
				padding: p2m(30);
				width: p2m(130);
				border-left: none;
				color: #999;
				background-color: #494949;
				border-bottom: 1px dashed #999;

			}
		}
	}
	
	thead {
		td {
			color: #fff;
			
			font:{
				weight: 600;
				size: p2m(70);
			}
			
			
			&:nth-of-type(4n+1) {
				font-size: p2m(32);
				font-weight: 300;
			}
			&:nth-of-type(4n+2) {
				background-color: #eb5a32;
				vertical-align: top;
			}
			&:nth-of-type(4n+3) {
				background-color: #dd3f31;
				vertical-align: top;
			}
			&:nth-of-type(4n) {
				background-color: #d71711;
				vertical-align: top;
			}
			
			.spMonth,.spDay{
				display: block;
				text-transform: capitalize;
				font-size: p2m(20);
				font-weight: 100;
				line-height: p2m(46);
				height: p2m(46);
				color: #e6e6e6;
				margin-bottom: p2m(12);
			}
			.spDay{
				margin-top: p2m(12);
				margin-bottom: 0;
			}
		}
	}
	
	tbody {
		td {
			background-color: #fff;
			
			&.noTime {
				background-color: #ddd;
				color: #aaa;
			}
		}
		
		&.mine {
			td {
				height: p2m(220);
				section {
					display: -webkit-flex;
					display: flex;
					-webkit-flex-direction: column;
					flex-direction: column;
					flex-wrap: nowrap;
					align-items: flex-start;
					align-content: stretch;
					justify-content: space-between;
				}
				
				div{
					-webkit-flex: 1;
					flex: 1;
					@include lineClip(1);
					width: p2m(190);
					height: 100%;
					margin: auto;
					border-radius: 5px;
					font-size: p2m(22);
					text-transform: capitalize;
					background:{
						repeat: no-repeat;
						position: p2m(20) center ;
					};
				}
				
				.divAddTo {
					border: 2px dashed #d71711;
					color: #d71711;
					font-size: p2m(30);
					padding-top: 0;
				}
				
				.divActivity{
					background-image: url("./ico-activity.png?__inline");
					color: #fd722d;
					border: 1px solid #fd722d;
					background-color: #ffdece;
					overflow:hidden;
					text-overflow:ellipsis;
					text-indent:p2m(80);
				}
				.divMeeting{
					background-image: url("./ico-meeting.png?__inline");
					color: #92be57;
					border: 1px solid #92be57;
					background-color: #e5eed7;
					overflow:hidden;
					text-overflow:ellipsis;
					text-indent:p2m(80);
				}

				.divAppointSpeed{
					background-image: url("./ico-appointmentSpeed.png?__inline");
					color: #f026f1;
					border: 1px solid #e6aff1;
					background-color: #fcedff;
					overflow:hidden;
					text-overflow:ellipsis;
					text-indent:p2m(80);
				}

				.divAppoint{
					background-image: url("./ico-appointment.png?__inline");
					color: #f1d27e;
					border: 1px solid #f1d27e;
					background-color: #fff8df;
					overflow:hidden;
					text-overflow:ellipsis;
					text-indent:p2m(80);
				}

				.divTempAppoint {
					background-image: url("./ico_tempappointment.png?__inline");
					color: #cccccc;
					border: 1px solid #eeeeee;
					background-color: #eeeeee;
					overflow:hidden;
					text-overflow:ellipsis;
					text-indent:p2m(80);
				}


				.divWalk{
					background-image: url("./ico-walk.png?__inline");
					color: #617dc3;
					border: 1px solid #617dc3;
					background-color: #d7e3ee;
					overflow:hidden;
					text-overflow:ellipsis;
					text-indent:p2m(80);
				}
				
				.gray,&.gray{
					color: #bbb;
					border: 1px solid #ddd;
					background-color: #f6f6f6;
					filter: grayscale(100%);
					-webkit-filter: grayscale(100%);
				}
				
				&.r1{
					div{
						height: p2m(200);
						line-height: p2m(200);

					}
				}
				&.r2{
					div{
						height: p2m(95);
						line-height: p2m(95);
						margin-bottom: 3px;
					}
				}
				&.r3{
					div{
						height: p2m(60);
						line-height: p2m(60);
						margin-bottom: 3px;
					}
				}
				&.r4{
					div{
						height: p2m(44);
						line-height: p2m(44);
						margin-bottom: 3px;
					}
				}
			}
		}
		
		&.rest {
			td {
				height: p2m(60);

				&.lunch {
					background-color: #ddd;
					color: #aaa;
				}
			}
		}
	}
	
}